import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';


import {
  EntityStateInterface, IsEntityChangedInterface,
  EntityChangeResultEnum, EntityStateEnum
} from '../model/entity-state.interface';

@Injectable()
export class EntityStateService implements EntityStateInterface {
  private isEntityChangedInit: IsEntityChangedInterface = {
    op: EntityStateEnum.NOSET,
    isEnd: false,
    opResult: EntityChangeResultEnum.NOSET
  };

  private isEntityChangedSub$ =
    new BehaviorSubject<IsEntityChangedInterface>(this.isEntityChangedInit);
  public isEntityChanged$ = this.isEntityChangedSub$.asObservable();
  public isEntityChangedValue = this.isEntityChangedSub$.value;

  public context = 'N/A';

  constructor() { }

  public reset() {
    this.isEntityChangedSub$.next(this.isEntityChangedInit);
  }
  public complete() {
    this.isEntityChangedSub$.complete();
  }
  public next(entityChangedState: IsEntityChangedInterface) {
    if (entityChangedState) {
      this.isEntityChangedSub$.next(entityChangedState);
    } else {
        this.isEntityChangedSub$.next(this.isEntityChangedInit);
    }
  }
}
