import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { EntityStateService } from './entity-state.service';
import {
  IsEntityChangedInterface, EntityMessageTypeEnum,
  EntityStateEnum, EntityChangeResultEnum
} from '../../shared/model/entity-state.interface';
import { ServerStatusService } from './server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EntityUpdateService {
  private apiUrl = environment.apiUrl;

  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,

  ) { }

  public approveLoanApp(clientId: number, loanAppObj: any, 
                          stateService: EntityStateService): void {
    console.log('SYNC ENTER EntityUpdateService.approveLoanApp() %O', loanAppObj);

    if (stateService) {
      const isLoanAppChangedEnter: IsEntityChangedInterface = {
        op: EntityStateEnum.UPDATE,
        isEnd: false,
        opResult: EntityChangeResultEnum.NOSET
      };
      stateService.next(isLoanAppChangedEnter);
    }
    this.http.post<any>(`${this.apiUrl}/clients/${clientId}/loans`, loanAppObj, {
      headers: new HttpHeaders()
        .set("Content-Type", "application/json")
        .set("Accept", "application/json")
    })
      .pipe(
        map((result: any) => {
          console.log('\tASYNC PIPE EntityUpdateService.approveLoanApp().map(ANY): %O', result);

          // if (this.isLoanProductNotFound(product)) {
          //   throw new Error(`Loan Product with [productId] = ${prodId} has not found.`);
          // }

          return { token: 'OK', result };
        }),
        catchError((err: any): any => {
          console.log('\tASYNC PIPE EntityUpdateService.approveLoanApp().catchError(): %O', err);

          let msg: string;
          let isLoanAppChangedError: IsEntityChangedInterface = null;
          if (stateService) {
            isLoanAppChangedError = {
              op: EntityStateEnum.UPDATE,
              isEnd: true,
              opResult: EntityChangeResultEnum.ERROR,
            };
          }
          if (err instanceof HttpErrorResponse) {
            // Server error 
            const status = err.status;
            if (status === 0) {
              msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
            } else if (status > 0 && status < 600) {
              msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
            }
            isLoanAppChangedError.messageType = EntityMessageTypeEnum.ERROR;
            isLoanAppChangedError.message = msg;
          } else {
            // Client error 
            msg = err.message ? err.message : err.toString();;
            isLoanAppChangedError.messageType = EntityMessageTypeEnum.ERROR;
            isLoanAppChangedError.message = `CLIENT ERROR: ${msg}`;
          }
          if (stateService) {
            stateService.next(isLoanAppChangedError);
          }          
          this.onDestroySub$.next(true);

          return of({ token: 'ERROR', result: null });
        }),
        takeUntil(this.onDestroySub$)
      ).subscribe(
        (rezObj: { token: string; result: any }) => {
          console.log(`\tSUBSCRIBE: EntityUpdateService.approveLoanApp().subscribe(NEXT): rezObj: %O`, rezObj);

          if (rezObj.token === 'OK') {
            
            if (stateService) {
              const isLoanAppChangedExit: IsEntityChangedInterface = {
                op: EntityStateEnum.UPDATE,
                isEnd: true,
                opResult: EntityChangeResultEnum.SUCCESS,
                messageType: EntityMessageTypeEnum.INFO,
                message: rezObj.result.message
              };
              stateService.next(isLoanAppChangedExit);
            }
          }
        }
        , err => console.log(`\tSUBSCRIBE: EntityUpdateService.approveLoanApp().subscribe(ERROR): %O`, err)
        , () => console.log(`\tSUBSCRIBE: EntityUpdateService.approveLoanApp().subscribe(COMPLETE)`)    
      );

    // eom 
  }
  // eoc  EntityUpdateService --------
}
