import { Observable } from 'rxjs';
import { EntityChangeResultEnum, EntityMessageTypeEnum } from '../shared/model/entity-state.interface';

export interface UserInterface {
    id?: number;
    name?: string;
    email?: string;
    role?: string;
}
export interface UserWithTokenInterface {
    user: UserInterface;
    token: string;
}
export const WrongUser: UserInterface = {
    id: -1,
    name: 'Wrong',
    email: 'Wrong',
    role: 'Wrong',
};
export const WrongUserWithJWT: UserWithTokenInterface = {
    token: '',
    user: null
};
export enum UserStateEnum {
    NOSET = 'NOSET',
    // ADD = 'ADD',
    // UPDATE = 'UPDATE',
    // DELETE = 'DELETE',
    SIGNUP = 'SIGNUP',
    LOGIN = 'LOGIN'
}
export interface IsUserChangedInterface {
    op: UserStateEnum;
    isEnd: boolean;
    opResult: EntityChangeResultEnum;
    userWithToken: UserWithTokenInterface;
    messageType?: EntityMessageTypeEnum;
    message?: string;
}
export interface UserStateInterface {
    context: string;
    isUserChanged$: Observable<IsUserChangedInterface>;
    isUserChangedValue: IsUserChangedInterface;
    reset(): void;
    complete(): void;
    next(state: IsUserChangedInterface): void;
  }
