import { Injectable } from '@angular/core';
import { WrongUserWithJWT, UserWithTokenInterface } from './user.interface';

// const TOKEN_KEY = 'auth-token';  // netesa -
const USER_KEY = 'auth-user';

@Injectable({
  providedIn: 'root'
})
export class TokenStorageService {

  constructor() { }

  signOut() {
    window.sessionStorage.clear();
  }
  // netesa -
  // public saveToken(token: string) {
  //   window.sessionStorage.removeItem(TOKEN_KEY);
  //   window.sessionStorage.setItem(TOKEN_KEY, token);
  // }

  // public getToken(): string {
  //   return sessionStorage.getItem(TOKEN_KEY);
  // }

  public saveUser(user) {
    window.sessionStorage.removeItem(USER_KEY);
    window.sessionStorage.setItem(USER_KEY, JSON.stringify(user));
  }

  public getUser(): UserWithTokenInterface  {
    let userInSession = sessionStorage.getItem(USER_KEY);
    if(userInSession === null) return null; // netesa - WrongUserWithJWT;
    return JSON.parse(userInSession);
  }
}
