import { Observable } from 'rxjs';

export enum EntityChangeResultEnum {
    NOSET = 'NOSET',
    ERROR = 'ERROR',
    SUCCESS = 'SUCCESS'
  }
export enum EntityMessageTypeEnum {
    INFO = 'INFO',
    WARN = 'WARN',
    ERROR = 'ERROR',
    FATAL = 'FATAL'
  }

  export enum EntityStateEnum {
    NOSET = 'NOSET',
    LOAD = 'LOAD',
    ADD = 'ADD',
    UPDATE = 'UPDATE',
    DELETE = 'DELETE',
    WRONG = 'WRONG'
  }
  export interface IsEntityChangedInterface {
    op: EntityStateEnum;
    isEnd: boolean;
    opResult: EntityChangeResultEnum;
    entity?: any;
    messageType?: EntityMessageTypeEnum;
    message?: string;
  }
  export interface EntityStateInterface {
    context: string;
    isEntityChanged$: Observable<IsEntityChangedInterface>;
    isEntityChangedValue: IsEntityChangedInterface;
  
    reset(): void;
    complete(): void;
    next(state: IsEntityChangedInterface): void;
  }
  
  export interface OpStateInterface {
    op: string;
    isEnd: boolean;
    opResult: string;
  }