import { NgModule } from '@angular/core';
import { PreloadAllModules, Routes, RouterModule, NoPreloading } from '@angular/router';

import { AuthGuard } from './shared/guard/auth.guard';

import { LoginComponent } from './auth/login/login.component';
import { HomeComponent } from './auth/home/home.component';
import { PageNotFoundComponent } from './page-not-found.component';

const routes: Routes = [
  {
    path: 'login',
    component: LoginComponent 
  },
  {
    path: 'home',
    component: HomeComponent,
    canActivate: [AuthGuard]
  }, 
  {
    path: 'clients',
    loadChildren: () => import('./clients/clients.module').then(mod => mod.ClientsModule),
    canLoad: [AuthGuard],    
  },
  {
    path: 'loanapplications',
    loadChildren: () => import('./loanapplications/loanapplications.module').then(mod => mod.LoanApplicationsModule),
    canLoad: [AuthGuard],
  },
  {
    path: 'loans',
    loadChildren: () => import('./loans/loans.module').then(mod => mod.LoansModule),
    canLoad: [AuthGuard],
  },
  {
    path: '', 
    redirectTo: '/login',
    pathMatch: 'full'
  },  
  { path: '**',
    component: PageNotFoundComponent
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes
    // ,{preloadingStrategy: NoPreloading, enableTracing: false, useHash: true}
    // ,{ enableTracing: true, useHash: true }
    // , {preloadingStrategy: PreloadAllModules}
    // , { preloadingStrategy: NoPreloading, useHash: true }
    , { preloadingStrategy: NoPreloading }
    )],
  exports: [RouterModule]
})
export class AppRoutingModule { }
