import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { LoanProductInterface, WrongLoanProduct } from '../loan-product.interface';

import { EntityStateService } from '../../../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, 
  EntityStateEnum, EntityChangeResultEnum } from '../../../shared/model/entity-state.interface';

import {ServerStatusService } from '../../../shared/services/server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LoanProductService {
  private apiUrl = environment.apiUrl;

  private curLoanProductSub$ = new Subject<any | LoanProductInterface>();
  public curLoanProduct$ = this.curLoanProductSub$.asObservable()
                        .pipe(
                          startWith(WrongLoanProduct),
                          shareReplay(1)
                        );
  public curLoanProduct: LoanProductInterface = WrongLoanProduct;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,

  ) { 
    this.curLoanProduct$.subscribe(
      product => {
        this.curLoanProduct = product;
      }
    );
  }
  // ------------------------------------------------------------
  public isLoanProductWrong(product: LoanProductInterface): boolean {
    if (!product) {
      throw new Error('Parameter [product] is null');
    }
    if (product === WrongLoanProduct) {
      return true;
    } else {
        return false;
    }
  }  
  public isCurLoanProductCorrect(): boolean {
    return !this.isLoanProductWrong(this.curLoanProduct);
  }
  public isCurLoanProductWrong(): boolean {
    return this.isLoanProductWrong(this.curLoanProduct);
  }

  public clearCurLoanProduct(): void {
    if (this.isCurLoanProductCorrect()) {
      this.curLoanProductSub$.next(WrongLoanProduct);
    }
  }
  public isLoanProductNotFound(product: any): boolean {
    if (!product) {
      throw new Error('Parameter [product] is null');
    }
    if (!product.productId || !product.productName) {
        throw new Error('Parameter [product] has no [productId] or [productName] fields.')
    } else if (product.productId === 0 && product.productName == null) {
        return true;
    } else {
      return false;
    }
  }  
// ------------------------------------------------------------

public loadLoanProduct(prodId: number, stateService: EntityStateService): void {
  // console.log('SYNC ENTER LoanProductService.loadLoanProduct() %O', stateService);

  if (stateService) {
    const isLoanProductChangedEnter: IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isLoanProductChangedEnter);
  }
  this.http.get<any>(`${this.apiUrl}/products/${prodId}`, {
    headers: new HttpHeaders()
      .set("Accept", "application/json")
  })
  .pipe(
    map((product: any) => {
      // console.log('\tASYNC PIPE LoanProductService.loadLoanProduct().map(ANY): %O', product);
      
      if (this.isLoanProductNotFound(product)) {
        throw new Error(`Loan Product with [productId] = ${prodId} has not found.`);
      }

      return {token: 'OK', product};
    }),
    catchError((err: any): any =>{
      // console.log('\tASYNC PIPE LoanProductService.loadLoanProduct().catchError() #2: %O', err);

      let msg: string;
      let isLoanProductChangedError: IsEntityChangedInterface = null;
      if (stateService) {
        isLoanProductChangedError = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isLoanProductChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoanProductChangedError.message = msg;
      } else {
        // Client error 
        msg = err.message ? err.message : err.toString(); ;
        isLoanProductChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoanProductChangedError.message = `CLIENT ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isLoanProductChangedError);
      }      
      this.clearCurLoanProduct();
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', product: null});
    }),
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; product: any} ) => {
      // console.log(`\tSUBSCRIBE: LoanProductService.loadLoanProduct().subscribe(NEXT): rezObj: %O`, rezObj);

       if (rezObj.token === 'OK') {
        this.curLoanProductSub$.next(rezObj.product); // BEFOR stateService.next()!
        if (stateService) {
          const isLoanProductChangedExit: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS
          };
          stateService.next(isLoanProductChangedExit);
        }
      }
    }
    // , err => console.log(`\tSUBSCRIBE: ClientListService.loadClientList().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: ClientListService.loadClientList().subscribe(COMPLETE)`)    
  );

}

// end
}
